#content {
	margin: 0;
}

#primary {
	margin: 0;
	padding: 2rem 0;
}
.entry-header {
	margin: 0 0 2rem 0;
}

.entry-meta {
	margin-bottom: 1.25rem;
}

.published + .updated {
	display: none;
}

.vcard {
	display: inherit;
	padding: 0;
	border: none;
	margin: 0;
}

.vcard .fn {
	font-weight: normal;
	font-size: 1rem;
}

.entry-content p {
	margin-bottom: 1.25rem;
}